.display {
  background: -webkit-gradient(linear, left top, right bottom, from(var(--purple)), to(var(--orange)));
  background: -webkit-linear-gradient(top left, var(--purple), var(--orange));
  background: -o-linear-gradient(top left, var(--purple), var(--orange));
  background: linear-gradient(to bottom right, var(--purple), var(--orange));
  -webkit-box-shadow: var(--box-shadow);
          box-shadow: var(--box-shadow);
  padding: 53px;
}

.displayInner {
  max-width: 500px;
  margin: auto;
}

.heading {
  color: var(--orange);
  text-align: left;
}

.body {
  color: white;
}

.gitHubButton {
  background-color: transparent;
  border: 2px solid white;
  border-radius: 4px;
  text-align: center;
  width: 145px;
  padding: 10px;
  -webkit-transition: background-color 0.3s, color 0.3s, -webkit-transform 0.5s;
  transition: background-color 0.3s, color 0.3s, -webkit-transform 0.5s;
  -o-transition: transform 0.5s, background-color 0.3s, color 0.3s;
  transition: transform 0.5s, background-color 0.3s, color 0.3s;
  transition: transform 0.5s, background-color 0.3s, color 0.3s, -webkit-transform 0.5s;
}

.gitHubButton:hover {
  -webkit-filter: none;
          filter: none;
  background-color: white;
  color: var(--charcoal);
}

@media (max-width: 500px) {
  .heading {
    color: var(--orange);
    text-align: center;
  }

  .display {
    text-align: center;
  }
}



