.project {
  max-width: 500px;
  background-color: white;
  text-align: center;
  -webkit-transition: -webkit-transform 0.5s, -webkit-filter 0.5s;
  transition: -webkit-transform 0.5s, -webkit-filter 0.5s;
  -o-transition: transform 0.5s, filter 0.5s;
  transition: transform 0.5s, filter 0.5s;
  transition: transform 0.5s, filter 0.5s, -webkit-transform 0.5s, -webkit-filter 0.5s;
  padding: 40px 40px 25px;
  -webkit-box-shadow: var(--box-shadow);
          box-shadow: var(--box-shadow);
  border: none;
  margin: 40px auto;
}

@media (max-width: 620px) {
  .project {
    margin: 20px;
  }
}

.projectName {
  color: white;
}

.projectBody {
  color: white;
  margin-bottom: 28px;
}

.button {
  background-color: transparent;
  border: 2px solid white;
  border-radius: 4px;
  text-align: center;
  width: 145px;
  padding: 10px;
  -webkit-transition: background-color 0.3s, color 0.3s, -webkit-transform 0.5s;
  transition: background-color 0.3s, color 0.3s, -webkit-transform 0.5s;
  -o-transition: transform 0.5s, background-color 0.3s, color 0.3s;
  transition: transform 0.5s, background-color 0.3s, color 0.3s;
  transition: transform 0.5s, background-color 0.3s, color 0.3s, -webkit-transform 0.5s;
  margin-bottom: 20px;
}

.button:hover {
  -webkit-filter: none;
          filter: none;
  background-color: white;
  color: var(--charcoal);
}



