.slideDown {
  opacity: 0;
}

.slideDownFinished {
  opacity: 1;
  -webkit-transition: opacity 0.2s, -webkit-transform 1.2s;
  transition: opacity 0.2s, -webkit-transform 1.2s;
  -o-transition: opacity 0.2s, transform 1.2s;
  transition: opacity 0.2s, transform 1.2s;
  transition: opacity 0.2s, transform 1.2s, -webkit-transform 1.2s;
}

.fadeInExit {
  opacity: 0;
}



